import React from 'react';
import { CurrentUserContext } from '../contexts/CurrentUserContext';
import PopupWithForm from './PopupWithForm';

function EditProfilePopup( {isOpen, onClose, onUpdateUser} ) {
    const currentUser = React.useContext(CurrentUserContext);
    const [name, setName] = React.useState('')
    const [description, setDescription] = React.useState('');
    
    function handleChangeName(e) {
        setName(e.target.value);
    };

    function hanldeChangeDescription(e) {
        setDescription(e.target.value);
    };

    function handleSubmit(e) {
        e.preventDefault();
        onUpdateUser({
            name: name,
            about: description
          });
    };

    React.useEffect(() => {
        setName(currentUser.name);
        setDescription(currentUser.about);
    }, [currentUser]);

    return (
        <PopupWithForm title="Ð ÐµÐ´Ð°ÐºÑÐ¸ÑÐ¾Ð²Ð°ÑÑ Ð¿ÑÐ¾ÑÐ¸Ð»Ñ" onClose={onClose} onSubmit={handleSubmit} name="changeProfile" isOpen={isOpen} >
            <button className="popup__close-button" type="reset" aria-label="ÐÐ°ÐºÑÑÑÑ" onClick={onClose}/>
            <h3 className="popup__heading">Ð ÐµÐ´Ð°ÐºÑÐ¸ÑÐ¾Ð²Ð°ÑÑ Ð¿ÑÐ¾ÑÐ¸Ð»Ñ</h3>
            <input id="input-name" value={name || ''} onChange={handleChangeName} name="name" className="popup__input" type="text" minLength="2" maxLength="40" required placeholder="ÐÐ¼Ñ" />
            <span id="input-name-error" className="popup__error"/>
            <input id="input-about"  value={description || ''} onChange={hanldeChangeDescription} name="about" className="popup__input" type="text" minLength="2" maxLength="200" required placeholder="Ð ÑÐµÐ±Ðµ" />
            <span id="input-about-error" className="popup__error"/>
            <button className="popup__submit-button" type="submit" aria-label="Ð¡Ð¾ÑÑÐ°Ð½Ð¸ÑÑ">Ð¡Ð¾ÑÑÐ°Ð½Ð¸ÑÑ</button>
        </PopupWithForm>
    );
};

export default EditProfilePopup;