import React from 'react';
import PopupWithForm from './PopupWithForm'; 

function AddPlacePopup({ isOpen, onClose, onAddPlace }) {
    const [cardName, setCardName] = React.useState('');
    const [cardLink, setLink] = React.useState('');

    function handleChangeCardName(e) {
        setCardName(e.target.value);
    };

    function handleChangeLink(e) {
        setLink(e.target.value)
    };

    function handleSubmit(e) {
        e.preventDefault();
        onAddPlace({
            name: cardName,
            link: cardLink
        });
    };

    return (
        <PopupWithForm onSubmit={handleSubmit} onClose={onClose} title="ÐÐ¾Ð²Ð¾Ðµ Ð¼ÐµÑÑÐ¾" name="createPhoto" isOpen={isOpen}>
                <button className="popup__close-button" type="reset" aria-label="ÐÐ°ÐºÑÑÑÑ" onClick={onClose}/>
                <h3 className="popup__heading">ÐÐ¾Ð²Ð¾Ðµ Ð¼ÐµÑÑÐ¾</h3>
                <input id="input-place" onChange={handleChangeCardName} name="name" className="popup__input" type="text" minLength="1" maxLength="30" required placeholder="ÐÐ°Ð·Ð²Ð°Ð½Ð¸Ðµ" />
                <span id="input-place-error" className="popup__error"/>
                <input id="input-link" onChange={handleChangeLink} name="link" className="popup__input" type="url" required placeholder="Ð¡ÑÑÐ»ÐºÐ° Ð½Ð° ÐºÐ°ÑÑÐ¸Ð½ÐºÑ" />
                <span id="input-link-error" className="popup__error"/>
                <button className="popup__submit-button" aria-label="Ð¡Ð¾Ð·Ð´Ð°ÑÑ" type="submit">Ð¡Ð¾Ð·Ð´Ð°ÑÑ</button>
        </PopupWithForm>
    )
};

export default AddPlacePopup;